body,
li,
ul,
span,
h1 {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding-top: 70px;

  &__title {
    margin-bottom: 20px;
    font-family: Arial, Helvetica, sans-serif;
  }
}

.Tabs {
  &__list {
    display: flex;
    flex-direction: row;
    list-style: none;
    gap: 30px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__button {
    width: 100px;
    height: 50px;

    &-active {
      background-color: bisque;
    }
  }

  &__content {
    font-family: Arial, Helvetica, sans-serif;
  }

}

